import React, { useState, useRef, useEffect } from "react"
import { Link, graphql } from "gatsby"
import kebabCase from "lodash.kebabcase"

import Bio from "../components/bio"
import Layout from "../components/layout"
import SEO from "../components/seo"
import Toc from "../components/toc"
import CommentBox from "../components/comment-box.js"

const useCurrentHeaderUrl = baseURI => {
  const option = { threshold: 1.0 }
  const headerElementsRef = useRef(document.querySelectorAll(".anchor-header"))
  const prevHeaders = useRef()
  const [visibleHeaders, setVisibleHeaders] = useState([])

  const onHeaderElementVisible = entries => {
    entries.forEach(({ target, isIntersecting }) => {
      // const headerURI = target.href.split(baseURI)[1]

      if (!isIntersecting) {
        setVisibleHeaders(prev => prev.filter(element => element !== target))

        return
      }

      setVisibleHeaders(prev =>
        [...prev, target].sort((a, b) => {
          const headerElements = Array.from(headerElementsRef.current)
          const indexOfA = headerElements.findIndex(element => element === a)
          const indexOfB = headerElements.findIndex(element => element === b)

          return indexOfA - indexOfB
        })
      )
    })
  }

  const intersectionObserverRef = useRef(
    new IntersectionObserver(onHeaderElementVisible, option)
  )

  useEffect(() => {
    headerElementsRef.current = document.querySelectorAll(".anchor-header")

    const observer = intersectionObserverRef.current
    const headerElements = headerElementsRef.current

    headerElements.forEach(element => {
      observer.observe(element)
    })

    return () => {
      observer.disconnect()
    }
  }, [])

  return visibleHeaders[0] && new URL(visibleHeaders[0].href).hash
  // useEffect(() => {
  //   const handleScroll = () => {
  //     let aboveHeaderUrl
  //     const currentOffsetY = window.pageYOffset
  //     const headerElements = document.querySelectorAll(".anchor-header")

  //     headerElements.find(elem => {
  //       const { top } = elem.getBoundingClientRect()
  //     })
  //     for (const elem of headerElements) {
  //       const { top } = elem.getBoundingClientRect()
  //       const elemTop = top + currentOffsetY
  //       const isLast = elem === headerElements[headerElements.length - 1]
  //       if (currentOffsetY < elemTop - HEADER_OFFSET_Y) {
  //         aboveHeaderUrl &&
  //           setCurrentHeaderUrl(aboveHeaderUrl.split(location.origin)[1])
  //         !aboveHeaderUrl && setCurrentHeaderUrl(undefined)
  //         break
  //       } else {
  //         isLast && setCurrentHeaderUrl(elem.href.split(location.origin)[1])
  //         !isLast && (aboveHeaderUrl = elem.href)
  //       }
  //     }
  //   }
  //   window.addEventListener("scroll", handleScroll)
  //   return () => {
  //     window.removeEventListener("scroll", handleScroll)
  //   }
  // }, [])
}

// const HEADER_OFFSET_Y = 116

const BlogPostTemplate = ({ data, location }) => {
  const post = data.markdownRemark
  const siteTitle = data.site.siteMetadata?.title || `Title`
  const { previous, next } = data
  const currentHeaderURI = useCurrentHeaderUrl(location.href)

  console.log(currentHeaderURI)
  return (
    <Layout location={location} title={siteTitle}>
      <SEO
        title={post.frontmatter.title}
        description={post.frontmatter.description || post.excerpt}
        image={post.frontmatter.cover}
      />
      <div>
        <Toc
          tableOfContents={post.tableOfContents}
          currentHeaderUrl={currentHeaderURI}
        />
        <article
          className="blog-post"
          itemScope
          itemType="http://schema.org/Article"
        >
          <header>
            <h1 itemProp="headline">{post.frontmatter.title}</h1>
            <p>{post.frontmatter.date}</p>
          </header>
          <section
            dangerouslySetInnerHTML={{ __html: post.html }}
            itemProp="articleBody"
          />
          <hr />
          {/* Section과 Footer 사이 */}
          <div className="tags">
            <ul>
              {post.frontmatter.tags
                ? post.frontmatter.tags.map(tag => (
                    <li key={kebabCase(tag)}>
                      <Link to={`/tags/${kebabCase(tag)}`}>
                        {kebabCase(tag)}
                      </Link>
                    </li>
                  ))
                : null}
            </ul>
          </div>
          <footer>
            <Bio />
          </footer>
        </article>
        <nav className="blog-post-nav">
          <ul
            style={{
              display: `flex`,
              flexWrap: `wrap`,
              justifyContent: `space-between`,
              listStyle: `none`,
              padding: 0,
            }}
          >
            <li>
              {previous && (
                <Link to={previous.fields.slug} rel="prev">
                  ← {previous.frontmatter.title}
                </Link>
              )}
            </li>
            <li>
              {next && (
                <Link to={next.fields.slug} rel="next">
                  {next.frontmatter.title} →
                </Link>
              )}
            </li>
          </ul>
        </nav>
        <CommentBox repo="greenblues1190/greenblues1190.github.io" />
      </div>
    </Layout>
  )
}

export default BlogPostTemplate

export const pageQuery = graphql`
  query BlogPostBySlug(
    $id: String!
    $previousPostId: String
    $nextPostId: String
  ) {
    site {
      siteMetadata {
        title
        siteUrl
      }
    }
    markdownRemark(id: { eq: $id }) {
      id
      excerpt(pruneLength: 160)
      html
      tableOfContents
      frontmatter {
        title
        date(formatString: "MMMM DD, YYYY")
        description
        tags
        cover
      }
    }
    previous: markdownRemark(id: { eq: $previousPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
    next: markdownRemark(id: { eq: $nextPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
  }
`
